@import "../utils/functions";
@import "../utils/placeholders";

// ==========================================================================
// Variables
// ==========================================================================

//
$hundred-percent: 100%;

// Medium query
$breakpoints: ('desk-qhd': 2560px, 'desk-hd': 1920px, 'desk-lg': 1600px, 'desk-ml': 1440px, 'desk-md': 1366px, 'desk-sm': 1280px, 'tab-lg': 992px, 'tab-sm': 768px, 'mob-lg': 640px, 'mob-md': 480px, 'mob-sm': 420px);

// Fonts style
// ==========================================================================
//$font-norm: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol" !default
//$font-alt: Georgia, serif !default

$font-norm: 'akrobat-regular', Helvetica, Arial, sans-serif !default;
$font-semibold: 'akrobat-semibold', Helvetica, Arial, sans-serif !default;
$font-bold: 'akrobat-bold', Helvetica, Arial, sans-serif !default;
$font-black: 'akrobat-black', Helvetica, Arial, sans-serif !default;
$font-light: 'akrobat-light', Helvetica, Arial, sans-serif !default;

// Reset font-weight and font-style
// Use for font-face
@mixin font-reset() {
	font: {
		weight: normal;
		style: normal; } }

// Main font
@mixin font-normal() {
	font-family: $font-norm;
	@include font-reset; }

@mixin font-bold() {
	font-family: $font-bold;
	@include font-reset; }

@mixin font-semibold() {
	font-family: $font-semibold;
	@include font-reset; }

@mixin font-italic() {
	font-style: italic; }

@mixin font-bold-italic() {
	font-weight: bold;
	font-style: italic; }

@mixin font-black() {
	font-family: $font-black;
	@include font-reset; }

@mixin font-light() {
	font-family: $font-light;
	@include font-reset; }

// Alternative font
@mixin font-alt() {
	font-family: $font-norm;
	@include font-reset; }

@mixin font-alt-bold() {
	font-family: $font-bold;
	@include font-reset; }

// Base
// ==========================================================================

$font-size: 1.8rem !default;
$line-height: round-float(3.0rem/$font-size, 2) !default;
// ----------------------------------------------------
$font-size--small: 1.4rem !default;
$line-height--small: round-float(2.4rem/$font-size--small, 2) !default;
// ----------------------------------------------------
$font-size--smallest: 1.2rem !default;
$line-height--smallest: round-float(2.0rem/$font-size--smallest, 2) !default;
// ----------------------------------------------------
$letter-spacing: 0.05em !default;

// Heading h1
$font-size-h1: 4.8rem !default;
$line-height-h1: round-float(6.0rem/$font-size-h1, 2) !default;
// ----------------------------------------------------
$font-size-h1--md: 3.0rem !default;
$line-height-h1--md: round-float(3.8rem/$font-size-h1--md, 2) !default;
// ----------------------------------------------------
$font-size-h1--sm: 2.6rem !default;
$line-height-h1--sm: round-float(3.2rem/$font-size-h1--sm, 2) !default;
// ----------------------------------------------------
@mixin font-h1() {
	font-size: $font-size-h1;
	line-height: $line-height-h1;

	@include media('<desk-sm') {
		font-size: $font-size-h1--md;
		line-height: $line-height-h1--md; }

	@include media('<mob-lg') {
		font-size: $font-size-h1--sm;
		line-height: $line-height-h1--sm; } }

// Heading h2
$font-size-h2: round-float($font-size-h1*3.6rem/4.8rem, 1) !default;
$line-height-h2: round-float(4.8rem/3.6rem, 2) !default;
// ----------------------------------------------------
$font-size-h2--md: round-float($font-size-h1--md*0.84, 1) !default;
$line-height-h2--md: round-float(($line-height-h1--md*1.02),2) !default;
// ----------------------------------------------------
$font-size-h2--sm: round-float($font-size-h1--sm*0.87, 1) !default;
$line-height-h2--sm: round-float(($line-height-h1--sm*1.01),2) !default;
// ----------------------------------------------------
@mixin font-h2() {
	font-size: $font-size-h2;
	line-height: $line-height-h2;

	@include media('<desk-sm') {
		font-size: $font-size-h2--md;
		line-height: $line-height-h2--md; }

	@include media('<mob-lg') {
		font-size: $font-size-h2--sm;
		line-height: $line-height-h2--sm; } }

// Heading h3
$font-size-h3: round-float($font-size-h1*2.4rem/4.8rem, 1) !default;
$line-height-h3: round-float(3.6rem/2.4rem, 2) !default;
// ----------------------------------------------------
$font-size-h3--sm: round-float($font-size-h1--sm*0.73, 1) !default;
$line-height-h3--sm: round-float(($line-height-h1--sm*1.05),2) !default;
// ----------------------------------------------------
@mixin font-h3() {
	font-size: $font-size-h3;
	line-height: $line-height-h3;

	@include media('<mob-lg') {
		font-size: $font-size-h3--sm;
		line-height: $line-height-h3--sm; } }

// Heading h4
$font-size-h4: round-float($font-size-h1*1.8rem/4.8rem, 1) !default;
$line-height-h4: round-float(3.0rem/1.8rem, 2) !default;

// Heading h5
$font-size-h5: $font-size !default;
$line-height-h5: $line-height !default;

// Heading h6
$font-size-h6: round-float($font-size*0.75, 1) !default;
$line-height-h6: round-float($line-height*1.25, 2) !default;

// Spaces
$bottom-space: round-float($font-size*$line-height, 1) !default;
//$bottom-space--section: round-float($font-size*$line-height*3, 1) !default
$bottom-space--section: 8rem !default;
$bottom-space--section--md: 5rem !default;
//$bottom-space--article: round-float($font-size*$line-height*2.0, 1) !default
$bottom-space--article: 5rem !default;
$bottom-space--article--md: 3rem !default;
$bottom-space--article--mob: 2.5rem !default;
//$bottom-space--element: round-float($font-size*$line-height*1.5, 1) !default
$bottom-space--element: 3rem !default;
$bottom-space--element--md: 2rem !default;
$bottom-space--element--mob: 1.5rem !default;
//$bottom-space--row: round-float($font-size*$line-height*0.75, 1) !default
$bottom-space--row: 1.5rem !default;


// Colors
// ==========================================================================

// Color of brands
$base-color: #ee1c25 !default;
$base-color-alt: #282828 !default;
// ----------------------------------------------------
$bg-color: #f2f2f2 !default;
$bg-color-alt: #fff !default;
// ----------------------------------------------------
$text-color: #282828 !default;
$text-color-light: #b2b2b2 !default;
$title-color: #262626 !default;
// ----------------------------------------------------
$link-color: $text-color !default;
// ----------------------------------------------------
$border-color: #e6e6e6 !default;
// ----------------------------------------------------
$mark-color: #ee1c25 !default;


// Skew
// ==========================================================================
$skew: -20deg;


// Class of typography styles
// ==========================================================================
// @example Sass - Использование
// .#{$user-content}
//
// @example css - Вывод CSS
// .user-content
$user-content: user-content !default;


// Layout
// ==========================================================================

// отступы для всего сайта
$page-layout-space: 50px 60px !default; // v h
$page-layout-space--md: 20px 30px !default; // v h
$page-layout-space--mob: 15px 15px !default; // v h
// максимальная ширина сайта
$page-layout-width: 1920px !default;
// минимальная ширина сайта
$page-layout-min: 320px !default;
// минимальные отступы у текствого блока
$text-layout-space: 50px 60px !default; // v h
$text-layout-space--md: 25px 30px !default; // v h
$text-layout-space--sm: 13px 15px !default; // v h
$text-layout-space--mob: 8px 10px !default; // v h
// максимальная ширина текстового блока
$text-layout-width: 800px !default;
// максимальная ширина текстового блока выходящего за пределы текста
$flood-layout-width: 1440px !default;
$flood-layout-width--md: 1440px !default;
$flood-layout-width--mob: 1440px !default;
// отступы внутри блочных элементов
$grid-space: 30px 30px !default; // v h
$grid-space--md: 20px 20px !default; // v h
$grid-space--mob: 10px 10px !default; // v h
// отступы в дефолтных списках
$list-space: 30px;

// Z-indexes
// ==========================================================================
$z-i-header: 9998 !default;
$z-i-nav: 9997 !default;
$z-i-main: 10 !default;
$z-i-footer: 5 !default;
$z-i-cover: 2 !default; // приподнять над остальным контентом


// Main section
// ==========================================================================
$main-section-space-vertical: $bottom-space--article $bottom-space--article;
$main-section-space-vertical--md: $bottom-space--article--md $bottom-space--article--md;
$main-section-space-vertical--mob: $bottom-space--article--mob $bottom-space--article--mob;


// Sliders
// ==========================================================================
$slider-button-size: 46px 46px; // h w
$slider-button-size--mob: 46px 46px; // h w
$slider-arrow-size: 34px;
$slider-arrow-size--mob: 28px;
// Promo Slider
$slider-promo-button-size: 60px 64px; // h w
$slider-promo-button-size--mob: 50px 50px; // h w


// Image mask
// ==========================================================================
$img-mask-bg: #2d2c2c;
@mixin image-mask() {
	&::before {
		@include pseudo();
		@include size(100%);
		@include position(0);
		//background-color: rgba($img-mask-bg, 0.35)
		//background-image: url(../../img/promo-slider-pattern.png)
		//background-size: 25% 25%
		z-index: -1;

		@include media('<desk-sm') {
			background-image: none; } } }

// Partials
// ==========================================================================
$parts-height: 202px;
$parts-animation-speed: 0.3s;
$parts-animation-speed-out: 0.45s;


// Products
// ==========================================================================
$products-space: 0 0 !default; // v h
$products-grid-space: 30px 30px !default; // v h
$products-grid-space--md: 20px 20px !default; // v h
$products-grid-space--mob: 15px 15px !default; // v h


// Header
// ==========================================================================
$header-height: 80px !default;
$header-height--md: 60px !default;
$header-height--sm: 50px !default;
$header-bg: #fff !default;
$header-shadow: 0 3px 6px rgba(black, 0.16) !default;

// Logo
//$logo-size: 50px 165px !default // h w
$logo-size: 56px 210px !default; // h w
//$logo-size--md: auto 132px !default // h w
$logo-size--md: auto 140px !default; // h w
//$logo-size--mob: auto 110px !default // h w
$logo-size--mob: auto 120px !default; // h w

// Search form
$search-form--font-size: 16px !default; // h w
$search-form--line-height: 20px !default; // h w
$search-form--size: 40px, 320px !default; // h w
$search-form--size--md: 40px, 280px !default; // h w
$search-form--size--mob: 50px, 100% !default; // h w
$search-form--opener-size: $header-height !default; // h w
$search-form--opener-size--md: $header-height--md !default; // h w
$search-form--opener-size--sm: $header-height--sm !default; // h w
$search-form--border: 1px !default; // h w

// Nav
$nav-width: 320px !default;
$nav-height: 49px;
$nav-space: 8px 24px !default;
$nav-space--sm: 8px 15px !default;
$nav-space--mob: 12px 15px !default;
$nav-arrow-size: 60px !default;
$nav-text-color: $text-color !default;
$nav-bg-color: #333 !default;
$nav-bg-color-s: #333 !default;
$nav-bg-color-ss: #333 !default;
$nav-bg-color-sss: #333 !default;
$nav-bg-color-ssss: #333 !default;


// Footer
// ==========================================================================
$footer-grid-space: 30px 0 !default; // v h


// Buttons
// ==========================================================================

$btn-font-size: 18px !default;
$btn-font-size--mob: 18px !default;
$btn-line-height: 22px !default;
$btn-line-height--mob: 22px !default;
$btn-height: 50px !default;
$btn-height--mob: 40px !default;
$btn-bg-color: $base-color !default;
$btn-bg-hover-color: darken($btn-bg-color, 20%) !default;
$btn-text-color: white !default;
$btn-disabled-color: #ccc !default;


// Forms
// ==========================================================================

// List of inputs
$inputs-list: 'input[type="text"]', 'input[type="password"]', 'input[type="email"]', 'input[type="search"]', 'textarea';

// Colors of forms
$form-text-color: $text-color !default;
$form-placeholder-color: lighten($form-text-color, 65%) !default;
$form-bg-color: white !default;
$form-border-color: lighten($form-text-color, 70%) !default;
// validation
$form-error-color: red !default;
$form-success-color: lightseagreen !default;
// disabled
$form-text-color--disabled: rgb(190, 190, 190) !default;
$form-bg-color--disabled: rgb(222, 222, 222) !default;
$form-border-color--disabled: rgb(222, 222, 222) !default;
// focus
$form-border-color--focus: darken($form-border-color, 20%) !default;

// Fonts of forms
$form-font-size: 16px !default;
$form-line-height: 24px !default;
$form-input-height: 50px !default;
$form-letter-spacing: null !default;

// @example Sass - Использование
// @example css - Вывод CSS
@mixin inputs-font() {
	//+font-alt
	font-family: inherit;
	font-size: $form-font-size;
	line-height: $form-line-height;
	@if $form-letter-spacing {
		letter-spacing: $form-letter-spacing; }
	color: $form-text-color; }

@mixin selects-font() {
	@include inputs-font; }

//+media('<mob-lg')
//ont-size: 10px

// Layout of forms
$form-border-width: 1px !default;
$form-input-space-vt: floor(($form-input-height - $form-line-height - $form-border-width*2)/2) !default; // верхний паддинг округляем в нижнюю сторону
$form-input-space-vb: ceil(($form-input-height - $form-line-height - $form-border-width*2)/2) !default; // нижний паддинг округляем в верхнюю сторону
$form-input-space-h: 18px !default;
$form-bottom-space: 3rem !default;
$form-bottom-space--mob: 2rem !default;
$form-gap: 40px !default;

// Vertical spaces
@mixin forms-space() {
	margin: 0 0 $form-bottom-space;

	@include media('<mob-lg') {
		margin: 0 0 $form-bottom-space--mob; } }

// Checkboxes
// Radio Buttons
$checkbox-color: #ff3aa3 !default;

// Select
$select-space-v: 12px !default;
$select-arrow-width: 40px !default;
$select-arrow-height: 100% !default;
$multiple-item-space: 3px !default;

// Style Selection
@mixin selectSelection() {
	height: $form-input-height;
	width: 100%;
	padding: $form-input-space-vt $select-arrow-width $form-input-space-vb $form-input-space-h;
	background: $form-bg-color;
	border: #{$form-border-width} solid #{$form-border-color};
	border-radius: 0;
	box-shadow: none;
	text-indent: 0;
	outline: none; }

// Select Arrow
@mixin selectArrow() {
	&::before, &::after {
		content: '';
		position: absolute;
		right: 22px;
		top: 50%;
		width: 8px;
		height: 2px;
		background-color: $text-color;
		margin-top: -1px;
		z-index: 1; }

	&::before {
		transform: rotate(45deg); }

	&::after {
		transform: rotate(-45deg);
		right: 17px; } }

// States
@mixin form-success() {
	color: $form-success-color;
	border-color: $form-success-color; }

@mixin form-error() {
	color: $form-error-color;
	border-color: $form-error-color; }

@mixin form-has-value() {
	color: blueviolet;
	border-color: blueviolet; }

@mixin form-on-focus() {
	//color: $form-border-focus-color
	border-color: $form-border-color--focus; }


// Animation
// ==========================================================================
$animation-speed: 0.2s !default;
$animation-speed-mouseleave: 0.35s !default;

// Social
// ==========================================================================
$facebook-color: #3a589e !default;
$vkontakte-color: #4b73a4 !default;
$twitter-color: #1da0f2 !default;
$odnoklassniki-color: #f2822e !default;
$youtube-color: #e62117 !default;
$instagram-color-start: #de4f5a !default;
$instagram-color-end: #a43b97 !default;
