/* user options */
.user-options {
	display: block;
	display: flex;
	position: absolute;
	right: 0;
	top: 0;
	font-family: "akrobat-regular", Helvetica, Arial, sans-serif;
	font-weight: normal;
	font-style: normal;
	color: #282828;
	line-height: 1;
}

.user-options__item {
	position: relative;
	display: inline-block;
	vertical-align: top;
	font-size: 1.8rem;
	line-height: 1.67;
	letter-spacing: 0.05em;
	min-width: 80px;
	height: 80px;
}

@media (max-width: 1439px) {
	.user-options__item {
		min-width: 60px;
		height: 60px;
	}
}

@media (max-width: 1279px) {
	.user-options__item {
		min-width: 50px;
		height: 50px;
	}
}

.user-options__item > a, .user-options__item > div {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100%;
	padding: 10px;
	color: inherit;
	line-height: 1;
	text-decoration: none;
	text-align: center;
}

.touchevents .user-options__item > a, .touchevents .user-options__item > div {
	user-select: none;
	-webkit-tap-highlight-color: transparent;
}

.user-options__item > a[data-title]::before, .user-options__item > div[data-title]::before {
	content: attr(data-title);
	display: block;
	min-width: 100%;
	padding: 4px 8px;
	border-radius: 2px;
	position: absolute;
	right: 0;
	top: 100%;
	font-family: "akrobat-light", Helvetica, Arial, sans-serif;
	font-weight: normal;
	font-style: normal;
	font-size: 1.4rem;
	line-height: 20px;
	text-align: center;
	letter-spacing: normal;
	z-index: 2;
	color: white;
	background-color: #ee1c25;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	opacity: 0;
	visibility: hidden;
	transform: translate(0, -5px);
	transform: translate3d(0, -5px, 0);
	will-change: opacity, transform;
	transition-property: opacity, transform;
	transition-duration: 0.2s;
}

.user-options__item > a svg, .user-options__item > div svg {
	display: inline-block;
	vertical-align: middle;
	width: 20px;
	height: 20px;
}

@media (max-width: 639px) {
	.user-options__item > a svg, .user-options__item > div svg {
		width: 16px;
		height: 16px;
	}
}

.user-options__item > a svg ~ span, .user-options__item > div svg ~ span {
	padding-left: 12px;
}

@media (max-width: 639px) {
	.user-options__item > a svg ~ span, .user-options__item > div svg ~ span {
		display: none;
	}
}

.user-options__item > a svg ~ span.hide-text, .user-options__item > div svg ~ span.hide-text {
	padding-left: 0;
}

.user-options__item > a path, .user-options__item > div path {
	fill: currentColor;
}

.user-options__item > a > span, .user-options__item > div > span {
	display: inline-block;
	vertical-align: middle;
	font-size: 1.6rem;
	line-height: 1;
	letter-spacing: normal;
	text-align: left;
}

.user-options__item > div {
	cursor: default;
}

.user-options__item > a {
	transition-property: color;
	transition-duration: 0.2s;
}

.user-options__item > a::after {
	content: "";
	display: block;
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	border-top: 2px solid #ee1c25;
	will-change: opacity;
	opacity: 0;
	transition-property: opacity;
	transition-duration: 0.2s;
}

.no-touchevents .user-options__item > a:hover {
	color: #ee1c25;
}

.no-touchevents .user-options__item > a:hover::after {
	opacity: 1;
}

.no-touchevents .user-options__item > a[data-title]:hover::before, .no-touchevents .user-options__item > div[data-title]:hover::before {
	opacity: 1;
	visibility: visible;
	transform: translate(0, -50%);
	transform: translate3d(0, -50%, 0);
}

.user-options__item_social {
	display: none;
}

.ms-drop__drop-js {
	opacity: 0;
	visibility: hidden;
}

.is-open .ms-drop__drop-js {
	opacity: 1;
	visibility: visible;
}

.lang-select__drop {
	position: absolute;
	left: 0;
	top: 100%;
	width: 100%;
	min-width: 80px;
	list-style: none;
	margin: 0;
	padding: 0;
	font-size: 1.6rem;
	line-height: 1;
	letter-spacing: normal;
	background-color: #fff;
	border: 1px solid #e6e6e6;
}

.lang-select__drop .active-item {
